.event {
  background: url(../img/event-bg.jpg) center top no-repeat;
  background-size: cover;
  padding: clamp(50px, 6vw, 100px) 0;

  .container {
    max-width: 1180px;
    margin: 0 auto; }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 30px 70px;

    @include md {
      gap: 20px; }

    @include sm {
      gap: 32px;
      flex-direction: column; } }

  &__left {
    max-width: 668px; }

  &__heading {
    @include light;
    font-size: 16px;
    margin: 0 0 20px; }

  &__title {
    position: relative;
    @include bold;
    font-size: clamp(42px, 5vw, 82px);
    line-height: 1;
    margin: 0 0 36px;

    &:after {
      content: '';
      position: absolute;
      left: -100px;
      top: 0;
      bottom: 0;
      width: 8px;
      background: #ff2407;

      @include tablet {
        left: -20px;
        width: 2px; }

      @include xxs {
        left: -14px; } } }

  &__descr {
    font-size: 16px;
    line-height: 1.6;

    p {

      margin: 0 0 24px;
      &:last-child {
        margin-bottom: 0; }
      &:first-child {
        font-size: 22px;
        @include sm {
          font-size: 18px; } } } }

  &__techs-title {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 40px;
    @include md {
      margin-bottom: 32px;
      font-size: 18px;
      color: $themeColor2; }

    @include sm {
      margin-bottom: 24px; }
    p {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      align-items: center;
      justify-content: center; }

    a {
      @extend .btn;
      @extend .btn--border;
      display: inline-flex;
      min-height: 50px;
      font-size: 14px;
      border-color: $themeColor2;
      color: $themeColor2;

      &:hover {
        text-decoration: none; } } }

  &__tip {
    font-size: 16px;
    line-height: 1.6;
    margin-top: 50px;

    @include xs {
      text-align: center;
      font-size: 18px; }
    p {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      align-items: center;
      @include xs {
        flex-direction: column;
        justify-content: center; } }

    a {
      @extend .btn;
      @extend .btn--border;
      display: inline-flex;
      min-height: 50px;
      min-width: 170px;
      font-size: 14px;
      border-color: $themeColor2;
      @include xs {
        width: 100%; }
      &:hover {
        text-decoration: none; } } } }
