.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	transition: background-color .3s ease;
	z-index: 11;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	transition: all ease 0.1s;

	@include md {
		opacity: 1;
		visibility: visible; }

	.container {
		position: relative;
		max-width: 1180px;

		@include md {
			width: 100%; } }

	&.live {
		top: auto;
		bottom: 0;

		@include md {
			bottom: auto;
			top: 0; } }

	&.is-sticky {
		background-color: $white;
		opacity: 1;
		visibility: visible;

		.nav__item {
			color: $black;
			&:hover {
				color: $black; } }

		.header {
			&__mini-logo {
				// transform: translate(0, 0)
				opacity: 1; }
			&__btn {
				.btn {
					background-color: $themeColor2;
					color: $black;
					&:hover {
						text-decoration: underline; } } } }

		@include md {
			background-color: transparent; } }

	&.is-open {
		@include md {
			pointer-events: auto;
			// background-color: $grayL

			.header {
				&__content {
					visibility: visible;
					opacity: 1;
					pointer-events: auto; }
				&__mini-logo {
					transform: translate(0, 0);
					display: none; }
				&__btn {
					.btn {
						background-color: $themeColor2;
						color: $black;
						padding: 0 20px; } }

				&__alt-btn {
					display: none; } }

			.burger {
				&__item {
					background: $black; } }

			.nav__item {
				color: $black;
				&:hover {
					color: $black; } }

			&:before {
				visibility: visible;
				opacity: 1;
				transform: scale(1);
				pointer-events: auto; } } }

	&__logo {
		display: none; }

	&__mini-logo {
		display: block;
		width: 50px;
		height: 50px;
		margin-right: auto;
		background: url(../img/logo_mini.svg);
		background-size: 100%;
		transition: all .2s; }
	&__inner {
		display: flex;
		align-items: center;
		width: 100%;

		@include md {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			z-index: 3;
			flex-direction: column;
			height: 100%;
			justify-content: flex-start;
			padding: 1px 0;
			overflow: hidden;
			overflow-y: auto;
			display: none;
			background: $grayL; }
		&.is-open {
			display: flex; } }

	&__socials {
		padding-left: 48px;
		min-width: 150px;
		@include md {
			min-width: unset; }
		@include tablet {
			padding-left: 37px; }
		@include md {
			padding: 0;
			margin-bottom: 20px;
			display: flex;
			justify-content: center; } }
	&__burger {
		display: none;
		@include md {
			display: flex;
			position: fixed;
			top: 10px;
			right: 10px;
			z-index: 4;
			pointer-events: auto; } }
	&__nav {
		@include md {
			position: relative;
			width: 100%;
			margin: auto 0 !important;
			padding: 20px 0;
			.nav {
				position: relative;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				overflow-y: auto;
				display: flex;
				flex-direction: column;
				align-items: center;
				flex-wrap: nowrap;

				&__item {
					color: $black; } } }

		&:not(:last-child) {
			@include md {
				margin-bottom: 20px; } } }
	&__btn {
		display: flex;
		justify-content: flex-end;

		.btn {
			min-height: 50px;
			font-size: 14px;
			padding: 0 12px;
			background: transparent;
			border-color: transparent;
			border-radius: 0;
			color: $themeColor2;

			&:hover {
				background: transparent;
				border-color: transparent;
				color: $themeColor2; } }

		@include tablet {
			min-width: 120px; }
		@include md {
			min-width: unset;
			display: block;
			margin-bottom: 20px; } }

	&__alt-btn {
		display: none;
		position: fixed;
		bottom: 0;
		right: 0;
		opacity: 0;
		visibility: hidden;
		transition: all ease 0.1s;

		&.visible {
			opacity: 1;
			visibility: visible; }

		@include md {
			display: block; }

		a {
			z-index: 3;
			height: 50px;
			font-size: 14px;
			padding: 14px 30px;
			pointer-events: auto; } } }

.nav {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	font-size: 14px;
	@include md {
		display: block; }
	&__item {
		height: 50px;
		align-items: center;
		justify-content: center;
		display: flex;
		text-decoration: none;
		color: $white;
		padding: 0 .7vw;
		transition: color .3s ease;
		@include hover {
			text-decoration: underline; }
		@include tablet {
			font-size: 14px; }
		@include md {
			align-items: center;
			justify-content: center;
			display: flex;
			text-align: center;
			width: 100%;

			&:hover {
				color: $black; } } } }

.burger {
	position: relative;
	right: 0;
	top: 0;
	-webkit-appearance: none;
	background-color: transparent;
	border: none;
	flex-direction: column;
	padding: 10px;
	&.is-active {
		.burger {
			&__item {
				&:nth-child(2n) {
					opacity: 0; }
				&:first-child {
					transform: translate(0, 8px) rotate(45deg); }
				&:last-child {
					transform: translate(0, -8px) rotate(-45deg); } } } }
	&__item {
		width: 28px;
		height: 3px;
		background-color: $white;
		transition: opacity .3s ease, transform .3s ease;
		&:not(:last-child) {
			margin-bottom: 5px; } } }
