.h-list {
  font-size: clamp(14px, 2.4vw, 20px);
  @include md {
    font-weight: 600; }

  &__item {
    display: flex;
    gap: 0 5px;

    @include xs {
      gap: 0; } }

  &__value {
    display: inline-flex;

    @include md {
      color: $themeColor2; } }

  &__text {
    display: inline-flex;
    gap: 0 5px;
    @include xs {
      display: block; }
    br {
      display: none; } }

  &__text-top {
    @include md {
      color: $themeColor2; } } }

.info-container {
  padding: 42px 0 24px;
  @include md {
    padding: 24px 0; }
  &__inner {
    display: flex;
    justify-content: center;
    @include xs {
      flex-direction: column;
      align-items: center; }
    .info {
      &:not(:last-child) {
        margin-right: 106px;
        @include sm {
          margin-right: 50px; }
        @include xs {
          margin: 0 0 20px; } } } } }
